import { Injectable } from '@angular/core';
import { Subject, Observable, Observer, Subscription, timer } from 'rxjs';


export interface ListItem {
  title: string;
  url: string;
}

@Injectable({
  providedIn: 'root'
})
export class ItemListService {

  public list: Subject<ListItem[]>;
  private listObserver: Observer<ListItem[]>;

  constructor() {
    this.list = new Subject<ListItem[]>();
  }


  private list$() {
    timer(500).subscribe(() => {
      this.list.next([
        { title: 'Goober', url: 'goober.com' },
        { title: 'Booger', url: 'booger.com' },
        { title: 'zat', url: 'zat.com' },
      ]);
    });
    return this.list.asObservable();
  }


}
